import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { 
    Container, Typography, Box, Card, CardContent, CardMedia, CardActions, 
    TextField, Button, Avatar, AppBar, Toolbar, IconButton, Grid, Paper, List, ListItem, ListItemText 
} from '@mui/material';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import ShareIcon from '@mui/icons-material/Share';
import CommentIcon from '@mui/icons-material/Comment';

const theme = createTheme({
    palette: {
        primary: {
            main: '#2e7d32',
        },
        secondary: {
            main: '#4caf50',
        },
        background: {
            default: '#f4f4f4',
        },
    },
    typography: {
        h1: {
            fontSize: '2.5rem',
            fontWeight: 700,
            color: '#2e7d32',
        },
        h2: {
            fontSize: '1.8rem',
            fontWeight: 600,
            color: '#388e3c',
        },
    },
});

function App() {
    const [user, setUser] = useState(null);
    const [posts, setPosts] = useState([]);
    const [photos, setPhotos] = useState([]);
    const [newPost, setNewPost] = useState('');
    const [imageFile, setImageFile] = useState(null);
    const [imagePreview, setImagePreview] = useState(null);
    const [comments, setComments] = useState({});

    useEffect(() => {
        axios.get('/users/emoorephp')
            .then(res => setUser(res.data))
            .catch(err => console.error(err));

        axios.get('/feed')
            .then(res => setPosts(res.data))
            .catch(err => console.error(err));

        axios.get('/users/emoorephp/photos')
            .then(res => setPhotos(res.data))
            .catch(err => console.error(err));
    }, []);

    const handlePostSubmit = async (e) => {
        e.preventDefault();
        let imageUrl = '';
        if (imageFile) {
            const reader = new FileReader();
            reader.readAsDataURL(imageFile);
            reader.onload = async () => {
                imageUrl = reader.result;
                try {
                    await axios.post('/posts', {
                        username: 'emoorephp',
                        content: newPost,
                        image_url: imageUrl,
                        hashtags: newPost.match(/#[a-zA-Z0-9]+/g) || [],
                        timestamp: new Date().toISOString()
                    });
                    setNewPost('');
                    setImageFile(null);
                    setImagePreview(null);
                    const res = await axios.get('/feed');
                    setPosts(res.data);
                } catch (err) {
                    console.error('Error creating post:', err);
                }
            };
            reader.onerror = (error) => {
                console.error('Error reading image:', error);
            };
        } else {
            try {
                await axios.post('/posts', {
                    username: 'emoorephp',
                    content: newPost,
                    image_url: '',
                    hashtags: newPost.match(/#[a-zA-Z0-9]+/g) || [],
                    timestamp: new Date().toISOString()
                });
                setNewPost('');
                const res = await axios.get('/feed');
                setPosts(res.data);
            } catch (err) {
                console.error('Error creating post:', err);
            }
        }
    };

    const handleImageChange = (e) => {
        const file = e.target.files[0];
        if (file) {
            setImageFile(file);
            const reader = new FileReader();
            reader.onloadend = () => {
                setImagePreview(reader.result);
            };
            reader.readAsDataURL(file);
        }
    };

    const handleLike = async (postId) => {
        try {
            await axios.post(`/posts/${postId}/like`);
            const res = await axios.get('/feed');
            setPosts(res.data);
        } catch (err) {
            console.error('Error liking post:', err);
        }
    };

    const handleShare = async (postId) => {
        try {
            await axios.post(`/posts/${postId}/share`);
            const res = await axios.get('/feed');
            setPosts(res.data);
        } catch (err) {
            console.error('Error sharing post:', err);
        }
    };

    const handleCommentSubmit = async (postId, e) => {
        e.preventDefault();
        const commentContent = comments[postId] || '';
        if (!commentContent) return;

        try {
            await axios.post(`/posts/${postId}/comment`, {
                username: 'emoorephp',
                content: commentContent,
            });
            setComments(prev => ({ ...prev, [postId]: '' }));
            const res = await axios.get('/feed');
            setPosts(res.data);
        } catch (err) {
            console.error('Error adding comment:', err);
        }
    };

    return (
        <ThemeProvider theme={theme}>
            <AppBar position="static">
                <Toolbar>
                    <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                        Amity
                    </Typography>
                    <Button color="inherit" href="/">Home</Button>
                    <Button color="inherit" href="/profile">Profile</Button>
                    <Button color="inherit" href="/photos">Photos</Button>
                </Toolbar>
            </AppBar>
            <Container sx={{ mt: 4, mb: 4 }}>
                {user && (
                    <Paper elevation={3} sx={{ p: 3, mb: 4 }}>
                        <Box display="flex" alignItems="center" gap={2}>
                            <Avatar
                                src="https://via.placeholder.com/100"
                                alt="Avatar"
                                sx={{ width: 100, height: 100 }}
                            />
                            <Box>
                                <Typography variant="h2">{user.username}</Typography>
                                <Typography>Email: {user.email}</Typography>
                                <Typography>Location: {user.location}</Typography>
                                <Typography>Followers: {user.followers}</Typography>
                            </Box>
                        </Box>
                    </Paper>
                )}
                <Typography variant="h2" gutterBottom>Create a Post</Typography>
                <Paper elevation={3} sx={{ p: 3, mb: 4 }}>
                    <form onSubmit={handlePostSubmit}>
                        <TextField
                            fullWidth
                            multiline
                            rows={3}
                            value={newPost}
                            onChange={(e) => setNewPost(e.target.value)}
                            placeholder="What's on your mind?"
                            variant="outlined"
                            sx={{ mb: 2 }}
                        />
                        <input
                            type="file"
                            accept="image/*"
                            onChange={handleImageChange}
                            style={{ marginBottom: '16px' }}
                        />
                        {imagePreview && (
                            <Box mb={2}>
                                <img src={imagePreview} alt="Preview" style={{ maxWidth: '100%', height: 'auto' }} />
                            </Box>
                        )}
                        <Button type="submit" variant="contained" color="primary">
                            Post
                        </Button>
                    </form>
                </Paper>
                <Typography variant="h2" gutterBottom>Feed</Typography>
                <Grid container spacing={3}>
                    {posts.map(post => (
                        <Grid item xs={12} key={post.id}>
                            <Card>
                                <CardContent>
                                    <Typography variant="body1">{post.content} by {post.username}</Typography>
                                    {post.image_url && (
                                        <CardMedia
                                            component="img"
                                            image={post.image_url}
                                            alt="Post"
                                            sx={{ mt: 2, maxHeight: 400, objectFit: 'contain' }}
                                        />
                                    )}
                                </CardContent>
                                <CardActions>
                                    <IconButton onClick={() => handleLike(post.id)} color="primary">
                                        <ThumbUpIcon /> {post.likes}
                                    </IconButton>
                                    <IconButton onClick={() => handleShare(post.id)} color="primary">
                                        <ShareIcon /> {post.shares}
                                    </IconButton>
                                </CardActions>
                                <CardContent>
                                    <Typography variant="body2" color="textSecondary">Comments</Typography>
                                    <List dense>
                                        {(post.comments || []).map((comment, index) => (
                                            <ListItem key={index}>
                                                <ListItemText
                                                    primary={comment.content}
                                                    secondary={`${comment.username} - ${comment.timestamp}`}
                                                />
                                            </ListItem>
                                        ))}
                                    </List>
                                    <form onSubmit={(e) => handleCommentSubmit(post.id, e)}>
                                        <TextField
                                            fullWidth
                                            variant="outlined"
                                            size="small"
                                            placeholder="Add a comment..."
                                            value={comments[post.id] || ''}
                                            onChange={(e) => setComments(prev => ({ ...prev, [post.id]: e.target.value }))}
                                            sx={{ mb: 1 }}
                                        />
                                        <Button type="submit" variant="contained" color="primary" size="small">
                                            Comment
                                        </Button>
                                    </form>
                                </CardContent>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
                <Typography variant="h2" gutterBottom sx={{ mt: 4 }}>Photos</Typography>
                <Grid container spacing={3}>
                    {photos.map(photo => (
                        <Grid item xs={12} sm={6} md={4} key={photo.id}>
                            <Card>
                                <CardMedia
                                    component="img"
                                    image={photo.image_url}
                                    alt={photo.caption}
                                    sx={{ height: 200, objectFit: 'cover' }}
                                />
                                <CardContent>
                                    <Typography variant="body2">{photo.caption}</Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            </Container>
        </ThemeProvider>
    );
}

export default App;